import React from 'react';
import classes from './Filter.module.css'
import Slider from '@material-ui/core/Slider'
import {FormControlLabel, Checkbox, Typography} from '@material-ui/core';



const Filter = (props) => {
  const marks = [
	  {
	    value: 0,
	    label: '0',
	  },
	  {
	    value: 100,
	    label: '1 million',
	  },
	];
	
	const types=props.types.map(entry=><li key={entry.alias}>
																		    	<FormControlLabel
																			        control={
																			          <Checkbox  checked={entry.visible} color="secondary" onChange={e=>props.changeCategory(e,entry.id)}/>
																			        }
																		        	label={<Typography variant="subtitle2">{entry.name}</Typography>}
																		      />
																      	</li>
	      											)	
  
  return (
    <div className={classes.root}>
    	<Typography variant="h5">Filterlar</Typography>
    	<div className={classes.CategoryFilter}>
    		<Typography variant="subtitle1">Kategoriyalar</Typography>
    		<ul>
    			{types}	
	      	
	      </ul>	  
    	</div>
    	<div className={classes.priceRangeWrapper}>
    		<Typography variant="subtitle1">Narx (10 000 ming so'mda)</Typography>
    		<div className={classes.priceRange}>
    		 	<Slider
		        aria-labelledby="discrete-slider-always"
		        step={1}
		        marks={marks}
		        valueLabelDisplay="on"
		        defaultValue={[0, 100]}
		        onChange={props.changePriceFilter}
		      />
		    </div>
    	</div>
    	{/*<div className={classes.comfortFilter}>
    		<Typography variant="subtitle1">Qulayliklar</Typography>
	    	<FormControlLabel
		        control={
		          <Checkbox  color="primary"/>
		        }
	        	label={<Typography variant="subtitle2">Oshxona</Typography>}
	      	/>    		
    	</div>*/}
    	
    	
    </div>
  )
}

export default Filter;