import React from 'react';
import classes from './Block.module.css'
// import {FullCont} from '../../HOCs'
import {Card} from '../../components'
// import {Link} from 'react-router-dom'


const Block = (props) => {
 
  const filtered=props.settings.data.filter(entry=>{
    let typeRes=false
      for (var i = 0; i < props.typeFilter.length; i++) {
        typeRes=(entry.type===props.typeFilter[i].alias&&props.typeFilter[i].visible?true:typeRes)
     }
    const priceRes=entry.price>=props.priceRange[0]&&entry.price<=props.priceRange[1]
    return typeRes&&priceRes
  })

  const list=filtered.map((dacha)=>{
  		return (
  			<div className="col-md-4"  key={dacha.id}>
  				<Card name={dacha.name} price={dacha.price} id={dacha.id}/>
  			</div>	
  		)})
  
  return (
    <section className={classes.root}>
    	<div className="row">
    		{list}		
    	</div>
    </section>
  )
}

export default Block;