import React from 'react';
import classes from './Card.module.css'
import defaultImage from '../../assets/dacha.jpg'
import {Paper} from '@material-ui/core'
import {Link, withRouter} from 'react-router-dom'
// import langContext from '../../context/langContext'
import StarIcon from '@material-ui/icons/Star';



const Card = (props) => {
  return (   
            <Link to={"/"+props.match.params.lang+"/dachalar/"+props.id}>
                <div className={classes.card}>
                    <Paper>
                        <div className={classes.cardHeader}>
                    	   <img src={defaultImage} alt="dacha"/>
                           <div className={classes.cardType}>
                            Dacha
                           </div>
                        </div>
                    	<div className={classes.cardDescription}>
                            <div className={classes.cardDescriptionTop}>
                        		<div className={classes.cardName}>
                        			{props.name}
                        		</div>
                               	<div className={classes.cardPrice}>
                        			{props.price}
                                </div>
                                <div className={classes.shortDescription}>
                                    {props.price}
                                </div>
                             </div>
                             <div className={classes.cardDescriptionBottom}>
                                <div className={classes.cardStars}>
                                  <StarIcon/> 
                                </div>
                             </div>
                        </div>
                    </Paper>
                </div>
            </Link>
            )
}

export default withRouter(Card);