import React, {Component} from 'react';
// import {Link} from 'react-router-dom'
import classes from './Dachalar.module.css'
import {FullCont} from '../../HOCs'
import {fullListUz, fullListRu} from '../../Data/list'
import {Block, Filter} from "../../components"

class Dachalar extends Component {
  state={
          settingsRu:{
              title:"Популярные дачи",
              anchorText:"Все дачи",
              anchorLink:this.props.match.params.lang+"/dachalar",
              data:fullListRu
          },
          settingsUz:{
              title:"Ko'p ko'rilgan dala hovlilar",
              anchorText:"Barcha dala hovlilar",
              anchorLink:this.props.match.params.lang+"/dachalar",
              data:fullListUz
          },
          typeFilter:[
            {
              id:1,
              name:"Sanatoriya",
              visible:true,
              alias:"sanatoriya"
            },
            {
              id:2,
              name:"Dachalar",
              visible:true,
              alias:"dacha"
            },
          ],
          priceFilter:[0,1000000]
        }
  catogoryFilterChangeHandle=(e, id)=>{
    const pIndex=this.state.typeFilter.findIndex((entry)=>entry.id===id)
    const newTypeFilter=[...this.state.typeFilter]
    newTypeFilter[pIndex].visible=!newTypeFilter[pIndex].visible
    this.setState({
      typeFilter:newTypeFilter
    })
    
  } 
  priceFilterChange=(e,[min,max])=>{
    this.setState({
      priceFilter:[min*10000, max*10000]
    })
    
  }   

  componentDidMount() {
    window.scrollTo(0,0)      
  }  
  render(){
      return (
        <section className={classes.root}>
        	<FullCont>
        		<div className="col-md-9">
        		   <Block priceRange={this.state.priceFilter} settings={this.props.match.params.lang==="uz"?this.state.settingsUz:this.state.settingsRu} typeFilter={this.state.typeFilter}/>
        		</div>
        		<div className="col-md-3">
        		   <Filter changePriceFilter={this.priceFilterChange} types={this.state.typeFilter} changeCategory={this.catogoryFilterChangeHandle}/>
        		</div>
        	</FullCont>
        </section>
      )
  }
}

export default Dachalar;