import React from 'react';
import classes from './PopularBlock.module.css'
import {FullCont} from '../../HOCs'
import {Card} from '../../components'
import {Link} from 'react-router-dom'

const PopularBlock = (props) => {
  const list=props.settings.data.map((dacha)=>{
  		return (
  			<div className="col-md-4"  key={dacha.id}>
  				<Card name={dacha.name} price={dacha.price} id={dacha.id}/>
  			</div>	
  		)})

  return (
    <section className={classes.root}>
    	<FullCont className={classes.head}>
    		<div className="col-md-6">
    			<div className={classes.title}>{props.settings.title}</div>
    		</div>
    		<div className="col-md-6">
    			<div className={classes.link}>
    				<Link to={props.settings.anchorLink}>{props.settings.anchorText}</Link>
    			</div>
    		</div>	
    	</FullCont>
    	<FullCont className={classes.body}>
    		{list}		
    	</FullCont>
    </section>
  )
}

export default PopularBlock;