import React, {useState} from 'react'
import {fullListUz, fullListRu} from '../../Data/list'
import classes from "./Dacha.module.css"
import {Typography, Chip, Button, } from "@material-ui/core"
import {FullCont} from "../../HOCs"
import {Link} from "react-router-dom"
import defaultImage from '../../assets/dacha.jpg'


const Dacha = (props) => {

	const lang=props.match.params.lang
	const id=props.match.params.id
	const item=(lang==="uz"?fullListUz.find((entry)=>entry.id===id):fullListRu.find(entry=>entry.id===id))
	const phone="7 777 77 77"

	const [phoneVisible, pChangeV]=useState(false)

  return (
  	<div className={classes.root}>
  		<FullCont>
  			<div className="col-md-12">
  				<div className={classes.toolKit}>
			    		<Link to={"/"+lang+"/dachalar"}>
			    			<Button color="primary" padding="10">Orqaga</Button>
			    		</Link>
			    </div>
  			</div>
  		</FullCont>
  		<FullCont>
  			<div className="col-md-12">
			    <div className={classes.mainCont}>
			    	
			    	<div className={classes.mainInfo}>
				    	<div className="row">	
				    		<div className="col-md-6">
				    			<div className={classes.dachaImage}>
				    				<img src={defaultImage} alt="dacha"/>
				    			</div>
				    		</div>
				    		<div className="col-md-6">
				    			<ul className={classes.info}>
				    				<li><Typography variant="h5" gutterBottom>{item.name}</Typography></li>
				    				<li>{item.price} so'm</li>
				    				<li>+998 9{phoneVisible?phone:"X XXX YY ZZ"} <Button color="primary" onClick={e=>pChangeV(true)}> telefon nomerni ko'rsatish</Button></li>
				    				<li className={classes.comfort}>
				    						<Chip label="Oshxona" color="secondary"/>
				    						<Chip label="Qishki Basseyn" color="secondary"/>
				    						<Chip label="Yozgi Basseyn" color="secondary"/>
				    						<Chip label="Sauna" color="secondary"/>
				    				</li>
				    			</ul>
				    		</div>
				    	</div>
			    	</div>
			    	<div className={classes.description}>
				    	<div className="row">
				    		<div className="col-md-12">
				    			{item.description}
				    		</div>
				    	</div>
			    	</div>
			    </div>
		    </div>
		  </FullCont>  
		</div>
  )
}

export default Dacha;