// import {FullCont} from '../../HOCs'
import {PopularBlock, Slider} from '../../components'
import {listUz, listRu} from '../../Data/list'

export class MainPage extends Component {
	state={
		settingsRu:{
			title:"Популярные дачи",
			anchorText:"Все дачи",
			anchorLink:this.props.match.params.lang+"/dachalar",
			data:listRu
		},
		settingsUz:{
			title:"Ko'p ko'rilgan dala hovlilar",
			anchorText:"Barcha dala hovlilar",
			anchorLink:this.props.match.params.lang+"/dachalar",
			data:listUz
		}
	}
	render() {
		
		return (
			<Fragment>
				<Slider/>
				<PopularBlock settings={this.props.match.params.lang==="uz"?this.state.settingsUz:this.state.settingsRu}/>
				<PopularBlock settings={this.props.match.params.lang==="uz"?this.state.settingsUz:this.state.settingsRu}/>
			</Fragment>
		);
	}
}

export default MainPage